* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

nav {
    background-color: skyblue;
}

.nav-list {
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;
    font-size: medium;
}

.nav-links {
    text-decoration: none;
    color: black;
    font-size: 18px;
}

.home,
.about,
.contact {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
    position: absolute;
    width: 100%;
    height: 90vh;
}

.home {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.about {
    background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
}

.contact {
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}

.home-content,
.about-content,
.contact-content {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    img {
        height: 70%;
    }
    h1 {
        font-size: 50px;
        margin: 30px 0px;
    }
    p {
        font-size: 20px;
    }
}